.cards {
    
 
    position:fixed;
    
  }
  
  #submit{
    width:20%;
    padding:10px;
    border-radius:25px;
    background-color:grey;
    color:black;;
    font-size:2s0px;
    font-style: italic;
    font-family:'Verdana';
    align-items:center;
  }
  h4 {
    text-align: center;
    font-size: 30px;
    color:rgb(130,130,130);
    font-family:'Courier New', 'Courier', 'monospace';
  }
    
  h2 {
    text-align:justify;
    padding-left:30%;
    font-size: 40px;
    color:black;
    font-family: Courier;
  }
    
  h1 {
    text-align: center;
    
    font-size: 50px;
    color: white;
    font-family: Courier;
  }
  .cards-btns {
    margin-top: 32px;
    border-radius: 20%;;
    align-items:center;

  }
  
  .cards-btns .btn {
    margin: 6px;

  }
  
  .cardo{
    background-color:grey;
    width:100%;
    font-family:'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', sans-serif;
    text-align:center;
    border : thick solid blanchedalmond;
    align-items:center;
  }
  .card-o{
    background-color:black;
    width:100%;
    font-family: 'Trebuchet MS';
    text-align:justify;
  }
  .cards__container {
    display: flex;
    flex-flow: column;
   
    max-width: 1120px;
    width: 25%;
    margin: 0 auto;
    background-color:transparent;
    border-radius:15%;
    
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
  }
  
  .cards__items {
    margin-bottom: 24px;
    
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
    
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
    
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  

  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }
  